.contacts{
    background-color: #FFE2E2;
    padding: 50px 0px;
}
.contacts__wrapper{
    border: 2px solid black;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.contacts__header{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    height: 70px;
}
.contacts__header >span{
    background-color: #D6D7DA;
}
.contacts__form{
    width: 50%;
    display: flex;
    flex-direction: column;
}
.contacts__button{
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

@media screen and (max-width: 375px){
    .contacts__header{
        border-bottom: none;
    }
    .contacts__wrapper{
        border: none;
    }
    .contacts__form{
        width: 80%;
    }
}

