.main{
    height: 100vh;
    background-color: #FFDEE9;
    padding: 50px 0;
}
.main__wrapper{
    border: 2px solid;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 90%;
    flex-wrap: wrap;
}
.text{
    background-color: #D8D8D8;
    display: flex;
    flex-direction: column;
    justify-content: center;

}
.photo{
    background-color: #D8D8D8;
    height: 400px;
    width: 300px;
}

@media screen and (max-width: 375px){
    .main{
        height: 80vh;
    }
    .main__wrapper{
        border: none;
        width: 100%;
    }
    .text{
        background-color: transparent;
        width: 100%;
    }
    .photo{
        width: 90%;
    }
}