.footer{
    background-color: #EBFFE4;
    padding: 50px 0px;
}
.footer__wrapper{
    border: 2px solid black;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 20px;
}
.footer__header{
    display: flex;
    justify-content: center;
    align-items: flex-start;
    height: 70px;
}
.footer__header >span{
    background-color: #D6D7DA;
}
.social{
    border: 2px solid black;
    display: flex;
    justify-content: space-between;
    width: 50%;
    margin-bottom: 50px;
}
.social__icon{
    width: 50px;
    height: 50px;
    background-color: #C4C4C4;
}
.footer__politic{
    display: flex;
    justify-content: center;
}
.footer__politic>span{
    background-color: #929292;
}

@media screen and (max-width: 375px){
    .footer__header{
        border-bottom: none;
    }
    .footer__wrapper{
        border: none;
    }
    .social{
        border: none;
        width: 100%;
    }
}

